:root {
  font-size: 10px;
  line-height: 1.5;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  box-sizing: border-box;
  --background-main: rgb(248, 248, 248);
  --background-darker: #f2f2f2;
  --dark-1: #000;
  --dark-2: rgba(0, 0, 0, 0.65);
  --dark-3: rgba(0, 0, 0, 0.48);
}

body {
  background-color: var(--background-main);
}

p,
a,
span {
  font-size: 1.6rem;
}

a {
  text-decoration: none;
  color: inherit;
}
a:visited, a:active {
  color: inherit;
}

.hidden {
  display: none;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 14px 24px;
}
@media screen and (max-width: 840px) {
  .header {
    padding: 16px;
  }
}
.header .brand-logo-container {
  font-weight: 600;
}
.header .brand-logo {
  font-size: 3rem;
  cursor: pointer;
}
.header .search-container {
  display: flex;
  width: 48px;
  height: 48px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 100%;
}

main {
  display: flex;
  flex-direction: column;
  margin: 66px auto 0 auto;
  padding: 0 42px;
  max-width: 1080px;
}
@media screen and (max-width: 840px) {
  main {
    padding: 0 16px;
  }
}

.hero-banner {
  margin-bottom: 10rem;
  max-width: 636px;
}
.hero-banner h1 {
  font-size: 5.8rem;
  margin: 0 0 2rem 0;
  cursor: pointer;
}
.hero-banner p {
  font-size: 1.8rem;
  margin: 0;
  color: var(--dark-2);
  text-align: justify;
}

.categories h2 {
  margin: 0 0 2.4rem 0;
  font-size: 3.2rem;
}
.categories .category-cards-container {
  display: flex;
  flex-direction: row;
  gap: 24px;
  overflow-x: scroll;
}
.categories .category-cards-container .category-card {
  display: flex;
  flex-direction: column;
  width: 196px;
  height: 240px;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  cursor: pointer;
}
.categories .category-cards-container .category-card img {
  width: 196px;
  height: 120px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.categories .category-cards-container .category-card span {
  padding: 24px;
  font-size: 2.4rem;
  font-weight: 500;
}

.article-previews-section {
  margin-top: 56px;
}
.article-previews-section .article-preview-header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}
.article-previews-section .article-preview-header h2 {
  font-size: 3.2rem;
  margin: 0 0 1rem 0;
}
.article-previews-section .article-preview-header button#sort-article-previews-btn {
  display: block;
  padding: 0;
  margin: 0;
  border: none;
  background-color: var(--background-main);
}
.article-previews-section .article-preview-header button#sort-article-previews-btn div {
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  border: 2px solid transparent;
  width: 200px;
  height: 40px;
  padding: 0 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.article-previews-section .article-preview-header button#sort-article-previews-btn div:focus {
  border: 2px solid black;
}
.article-previews-section .article-preview-header button#sort-article-previews-btn div span {
  font-size: 1.6rem;
}
.article-previews-section .article-preview-header button#sort-article-previews-btn div img {
  transform: rotate(90deg);
  filter: invert(38%) sepia(0%) saturate(1064%) hue-rotate(251deg) brightness(101%) contrast(77%);
}
.article-previews-section .article-preview-header #sort-article-previews-modal {
  transition: opacity 0.3s ease-in-out;
  position: absolute;
  right: 42px;
  top: 800px;
  background-color: white;
  border: 1px solid #ccc;
  width: 240px;
  z-index: 10;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 12px;
}
.article-previews-section .article-preview-header #sort-article-previews-modal ul {
  list-style-type: none;
  padding: 4px;
  margin: 0;
}
.article-previews-section .article-preview-header #sort-article-previews-modal ul li {
  padding: 14px;
  font-size: 1.4rem;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}
.article-previews-section .article-preview-header #sort-article-previews-modal ul li:hover {
  background-color: var(--background-main);
}
.article-previews-section .article-previews-container {
  display: flex;
  margin-top: 24px;
  gap: 24px;
  flex-wrap: wrap;
  justify-content: flex-start;
}
@media screen and (max-width: 600px) {
  .article-previews-section .article-previews-container {
    justify-content: center;
  }
}
.article-previews-section .article-previews-container a div.article-preview-card {
  box-sizing: border-box;
  width: 344px;
  cursor: pointer;
  padding: 12px;
  border-radius: 8px;
  background-color: white;
  display: flex;
  flex-direction: column;
}
.article-previews-section .article-previews-container a div.article-preview-card img {
  height: 180px;
  object-fit: cover;
  border-radius: 8px;
}
.article-previews-section .article-previews-container .article-preview-title {
  margin-top: 14px;
  padding: 0 8px;
  font-size: 18px;
  font-weight: 700;
}
.article-previews-section .article-previews-container .article-preview-content {
  margin-top: 8px;
  padding: 0 8px;
  font-size: 16px;
  font-weight: 400;
  color: var(--dark-3);
  line-height: 20px;
}
.article-previews-section .article-previews-container .article-preview-card-footer {
  margin-top: 24px;
  padding: 0 8px 6px 8px;
  display: flex;
  justify-content: space-between;
}
.article-previews-section .article-previews-container .article-preview-card-footer .article-preview-date {
  color: var(--dark-3);
  font-size: 1.4rem;
}

.tag {
  display: inline-block;
  padding: 2px 6px;
  background-color: var(--background-darker);
  border-radius: 4px;
  align-items: center;
  color: black;
}
.tag:not(:last-child) {
  margin-right: 10px;
}
.tag span {
  font-size: 1.4rem;
  font-weight: 500;
}

.column-layout {
  display: flex;
}
.column-layout aside {
  width: 20%;
}

.full-article {
  width: 50%;
}
.full-article .article-header {
  padding-top: 24px;
  color: var(--dark-3);
}
.full-article .article-header h1 {
  font-size: 56px;
  font-weight: 700;
  margin: 36px 0 0 0;
  color: black;
}
.full-article .article-header div.writer-date {
  display: flex;
  justify-content: space-between;
}
.full-article .article-header div.writer-date span {
  padding-top: 24px;
  font-size: 2rem;
  color: var(--dark-3);
}
.full-article .article-header div.tags-container {
  padding-top: 12px;
}
.full-article .article-header div.image-container {
  margin: 24px 0 12px 0;
}
.full-article .article-header div.image-container img {
  height: 190px;
  width: 100%;
  border-radius: 12px;
  object-fit: cover;
  display: block;
}

.article-content h2,
.article-content h3,
.article-content h4 {
  font-weight: 600;
}
.article-content h2 {
  font-size: 3.2rem;
  margin: 36px 0 18px 0;
}
.article-content h3 {
  font-size: 2.4rem;
  margin: 24px 0 12px 0;
}
.article-content h4 {
  font-size: 1.8rem;
  margin: 18px 0 4px 0;
}
.article-content p {
  margin: 0 0 12px 0;
  font-size: 1.8rem;
  line-height: 1.625;
}
.article-content figure {
  text-align: center;
}
.article-content figure img {
  max-width: 350px;
  border-radius: 12px;
  object-fit: cover;
}

aside.recent-articles {
  position: sticky;
  top: 0;
  height: min-content;
}
aside.recent-articles ul {
  list-style-type: none;
  margin: 0;
  padding: 70px 0 0 40px;
}
aside.recent-articles ul li {
  font-size: 18px;
  font-weight: 500;
  padding: 10px 0;
}

nav.breadcrumbs ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
nav.breadcrumbs ul li {
  display: inline-block;
  padding: 8px;
  font-size: 18px;
  font-weight: 400;
}
nav.breadcrumbs ul li:not(:last-child)::after {
  content: ">";
  color: var(--dark-3);
  padding-left: 16px;
}
nav.breadcrumbs ul li a {
  font-size: 18px;
  font-weight: 400;
}
nav.breadcrumbs ul li a:hover {
  color: var(--dark-3);
}

footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 64px;
  height: 111px;
  gap: 8px;
  background-color: black;
  color: white;
  font-size: 16px;
  font-weight: 400;
}
footer ul {
  padding: 0;
}
footer ul li {
  display: inline-block;
  list-style-type: none;
  font-weight: 500;
}
footer ul li:not(:first-child) {
  margin-left: 0.4rem;
}